/**
 * 
 * 
 */

interface VoiceProfile {
  id: string;
  name: string;
  type: 'default' | 'custom';
  recordings?: { [key: string]: string }; // key: audio name, value: blob URL
}

export class AudioPlayer {
  private static instance: AudioPlayer;
  private audioCache: Map<string, HTMLAudioElement> = new Map();
  private isEnabled: boolean = true;
  private currentVoiceProfile: string = 'default';
  private voiceProfiles: VoiceProfile[] = [];
  private customAudioCache: Map<string, HTMLAudioElement> = new Map();
  private isAndroid: boolean = false;
  private audioContext: AudioContext | null = null;
  private isAudioInitialized: boolean = false;
  private currentlyPlaying: HTMLAudioElement | null = null;
  private browserCompatibility: {
    audioSupport: boolean;
    oggSupport: boolean;
    mp3Support: boolean;
    webAudioSupport: boolean;
  } = {
    audioSupport: false,
    oggSupport: false,
    mp3Support: false,
    webAudioSupport: false
  };

  private constructor() {
    this.detectBrowserCompatibility();
    this.detectAndroid();
    this.loadVoiceProfiles();
    this.initializeAudioContext();
  }

  public static getInstance(): AudioPlayer {
    if (!AudioPlayer.instance) {
      AudioPlayer.instance = new AudioPlayer();
    }
    return AudioPlayer.instance;
  }

  /**
   * 
   */
  private detectBrowserCompatibility(): void {
    // 
    this.browserCompatibility.audioSupport = typeof Audio !== 'undefined';
    
    if (this.browserCompatibility.audioSupport) {
      const audio = new Audio();
      
      // OGG
      this.browserCompatibility.oggSupport = audio.canPlayType('audio/ogg') !== '';
      
      // MP3
      this.browserCompatibility.mp3Support = audio.canPlayType('audio/mpeg') !== '';
    }
    
    // Web Audio API
    this.browserCompatibility.webAudioSupport = 
      typeof AudioContext !== 'undefined' || typeof (window as any).webkitAudioContext !== 'undefined';
    
    console.log(' [] :', this.browserCompatibility);
    
    // 
    if (!this.browserCompatibility.audioSupport) {
      console.warn(' [] HTML5');
    }
    
    if (!this.browserCompatibility.oggSupport && !this.browserCompatibility.mp3Support) {
      console.warn(' [] OGGMP3');
    }
  }

  /**
   * Android
   */
  private detectAndroid(): void {
    this.isAndroid = /Android/i.test(navigator.userAgent) || 
                     (window as any).Capacitor?.isNativePlatform() === true;
    console.log('Android:', this.isAndroid);
  }

  /**
   * 
   */
  private initializeAudioContext(): void {
    if (this.isAndroid && !this.audioContext) {
      try {
        this.audioContext = new (window.AudioContext || (window as any).webkitAudioContext)();
        console.log('');
      } catch (error) {
        console.warn(':', error);
      }
    }
  }

  /**
   * Android
   */
  public async unlockAudio(): Promise<void> {
    if (this.isAndroid && this.audioContext && this.audioContext.state === 'suspended') {
      try {
        await this.audioContext.resume();
        this.isAudioInitialized = true;
        console.log('');
      } catch (error) {
        console.warn(':', error);
      }
    } else if (!this.isAndroid) {
      this.isAudioInitialized = true;
    }
  }

  /**
   * 
   */
  private loadVoiceProfiles(): void {
    const savedProfiles = localStorage.getItem('voiceProfiles');
    if (savedProfiles) {
      this.voiceProfiles = JSON.parse(savedProfiles);
    } else {
      this.voiceProfiles = [{ id: 'default', name: '', type: 'default' }];
    }
    
    const savedSelectedProfile = localStorage.getItem('selectedVoiceProfile');
    if (savedSelectedProfile) {
      this.currentVoiceProfile = savedSelectedProfile;
    }
  }

  /**
   * 
   */
  public getCurrentVoiceProfile(): string {
    return this.currentVoiceProfile;
  }

  /**
   * 
   */
  public setCurrentVoiceProfile(profileId: string): void {
    this.currentVoiceProfile = profileId;
    localStorage.setItem('selectedVoiceProfile', profileId);
  }

  /**
   * 
   */
  public getVoiceProfiles(): VoiceProfile[] {
    return this.voiceProfiles;
  }

  /**
   * 
   */
  public getBrowserCompatibility() {
    return { ...this.browserCompatibility };
  }

  /**
   * 
   */
  private checkAudioCompatibility(): boolean {
    if (!this.browserCompatibility.audioSupport) {
      console.error(' [] HTML5');
      return false;
    }
    
    if (!this.browserCompatibility.oggSupport && !this.browserCompatibility.mp3Support) {
      console.error(' [] ');
      return false;
    }
    
    return true;
  }

  /**
   * 
   */
  private getBestAudioFormat(): string {
    if (this.browserCompatibility.oggSupport) {
      return 'ogg';
    } else if (this.browserCompatibility.mp3Support) {
      return 'mp3';
    }
    return 'ogg'; // 
  }

  /**
   * URL
   * Web(HTTP) /audio/...
   * WebView(resource://) audio/...
   */
  private buildAudioSrc(audioName: string, format: string): string {
    try {
      const isHttpEnv = typeof window !== 'undefined' && typeof window.location !== 'undefined'
        && (window.location.protocol === 'http:' || window.location.protocol === 'https:');
      const base = isHttpEnv ? '/audio' : 'audio';
      return `${base}/${audioName}.${format}`;
    } catch (_) {
      // 
      return `audio/${audioName}.${format}`;
    }
  }

  /**
   * 
   */
  public preloadAudio(audioName: string): void {
    if (this.audioCache.has(audioName)) {
      return;
    }

    // 
    const format = this.getBestAudioFormat();
    const audio = new Audio(this.buildAudioSrc(audioName, format));
    audio.preload = 'auto';
    audio.volume = 0; // 0
    
    audio.onerror = () => {
      console.warn(` [] ${format}: ${audioName}`);
      // 
      const fallbackFormat = format === 'ogg' ? 'mp3' : 'ogg';
      const fallbackAudio = new Audio(this.buildAudioSrc(audioName, fallbackFormat));
      fallbackAudio.preload = 'auto';
      fallbackAudio.volume = 0;
      
      fallbackAudio.oncanplaythrough = () => {
        fallbackAudio.volume = 1;
        this.audioCache.set(audioName, fallbackAudio);
      };
      
      fallbackAudio.onerror = () => {
        console.error(` [] : ${audioName}`);
        // 
      };
    };
    
    // 
    audio.oncanplaythrough = () => {
      audio.volume = 1;
      this.audioCache.set(audioName, audio);
    };
    
    // 
    // this.audioCache.set(audioName, audio); // oncanplaythrough
  }

  /**
   * 
   */
  public async playAudio(audioName: string, playbackRate: number = 1.0): Promise<void> {
    console.log(` [DEBUG] playAudio : ${audioName}, : ${playbackRate}`);
    
    if (!this.isEnabled) {
      console.log(` [DEBUG] : ${audioName}`);
      return;
    }

    // 
    if (!this.checkAudioCompatibility()) {
      console.error(` [] : ${audioName}`);
      throw new Error(`: ${audioName}`);
    }

    // Android
    if (this.isAndroid && !this.isAudioInitialized) {
      console.log(` [DEBUG] Android`);
      await this.unlockAudio();
    }

    console.log(` [DEBUG] : ${audioName}, : ${this.currentVoiceProfile}`);
    
    try {
      // 
      if (this.currentVoiceProfile !== 'default') {
        console.log(` [DEBUG] : ${audioName}`);
        const customAudio = await this.playCustomAudio(audioName, playbackRate);
        if (customAudio) {
          console.log(` [DEBUG] : ${audioName}`);
          return; // 
        }
      }
      
      console.log(` [DEBUG] : ${audioName}`);
      // 
      await this.playDefaultAudio(audioName, playbackRate);
      console.log(` [DEBUG] : ${audioName}`);
    } catch (error) {
      console.error(` [ERROR] : ${audioName}`, error);
      throw error;
    }
  }

  /**
   * 
   */
  private async playCustomAudio(audioName: string, playbackRate: number = 1.0): Promise<boolean> {
    const profile = this.voiceProfiles.find(p => p.id === this.currentVoiceProfile);
    if (!profile || !profile.recordings || !profile.recordings[audioName]) {
      console.log(`: ${audioName}`);
      return false;
    }

    try {
      const cacheKey = `${this.currentVoiceProfile}_${audioName}`;
      let audio = this.customAudioCache.get(cacheKey);
      
      if (!audio) {
        audio = new Audio(profile.recordings[audioName]);
        this.customAudioCache.set(cacheKey, audio);
      }
      
      audio.currentTime = 0;
      audio.playbackRate = playbackRate;
      await audio.play();
      console.log(`: ${audioName}`);
      return true;
    } catch (error) {
      console.warn(`: ${audioName}`, error);
      return false;
    }
  }

  /**
   * 
   */
  private async playDefaultAudio(audioName: string, playbackRate: number = 1.0): Promise<void> {
    try {
      let audio = this.audioCache.get(audioName);
      // 
      // NETWORK_NO_SOURCE
      if (audio && (audio as any).error) {
        try {
          audio.pause();
        } catch {}
        this.audioCache.delete(audioName);
        audio = undefined as any;
      }
      
      if (!audio) {
        console.log(`: ${audioName}`);
        // 
        const format = this.getBestAudioFormat();
        audio = new Audio(this.buildAudioSrc(audioName, format));
        audio.volume = 0; // 0
        
        // Android
        if (this.isAndroid) {
          audio.preload = 'auto';
          audio.crossOrigin = 'anonymous';
        }
        
        // Android
        if (!this.isAndroid) {
          audio.preload = 'auto';
        }
        
        audio.onerror = (e) => {
          console.warn(` [] ${format}: ${audioName}`, e);
          // 
          const fallbackFormat = format === 'ogg' ? 'mp3' : 'ogg';
          const fallbackAudio = new Audio(this.buildAudioSrc(audioName, fallbackFormat));
          fallbackAudio.volume = 0;
          
          if (this.isAndroid) {
            fallbackAudio.preload = 'auto';
            fallbackAudio.crossOrigin = 'anonymous';
          }
          
          const replaceWithFallback = () => {
            this.audioCache.set(audioName!, fallbackAudio);
            // audio
            audio = fallbackAudio;
            console.log(` [] ${audioName} ${fallbackFormat}`);
          };
          
          fallbackAudio.oncanplaythrough = () => {
            replaceWithFallback();
          };
          
          fallbackAudio.onloadeddata = () => {
            if (fallbackAudio.readyState >= 2) { // HAVE_CURRENT_DATA
              replaceWithFallback();
            }
          };
          
          fallbackAudio.onerror = () => {
            console.error(` [] : ${audioName}`);
            // 
          };
        };
        
        // 
        let audioCached = false;
        
        // 
        const cacheOnSuccess = () => {
          if (!audioCached) {
            this.audioCache.set(audioName, audio);
            audioCached = true;
            console.log(` [] : ${audioName}`);
          }
        };
        
        audio.oncanplaythrough = cacheOnSuccess;
        audio.onloadeddata = () => {
          if (audio.readyState >= 2) { // HAVE_CURRENT_DATA
            cacheOnSuccess();
          }
        };
      } else {
        console.log(`: ${audioName}`);
      }

      // 
      if (audio.readyState === 0) {
        // 
        await new Promise((resolve, reject) => {
          const onCanPlay = () => {
            audio.removeEventListener('canplay', onCanPlay);
            audio.removeEventListener('error', onError);
            resolve(void 0);
          };
          const onError = (e: Event) => {
            audio.removeEventListener('canplay', onCanPlay);
            audio.removeEventListener('error', onError);
            reject(new Error(`: ${audioName}`));
          };
          audio.addEventListener('canplay', onCanPlay);
          audio.addEventListener('error', onError);
        });
      }

      // 
      if (this.currentlyPlaying && !this.currentlyPlaying.paused && this.currentlyPlaying !== audio) {
        console.log(` `);
        this.currentlyPlaying.pause();
        this.currentlyPlaying.currentTime = 0;
      }

      // 
      audio.volume = 1;

      // 
      audio.currentTime = 0;
      
      // 
      audio.playbackRate = playbackRate;
      
      // 
      this.currentlyPlaying = audio;
      
      console.log(` : ${audioName}, : ${playbackRate}, readyState: ${audio.readyState}`);
      
      // Android
      if (this.isAndroid) {
        await this.playWithRetry(audio, audioName);
      } else {
        await audio.play();
        
        // 
        await new Promise<void>((resolve) => {
          const onEnded = () => {
            audio.removeEventListener('ended', onEnded);
            // 
            if (this.currentlyPlaying === audio) {
              this.currentlyPlaying = null;
            }
            resolve();
          };
          audio.addEventListener('ended', onEnded);
          
          // resolve
          if (audio.ended) {
            if (this.currentlyPlaying === audio) {
              this.currentlyPlaying = null;
            }
            resolve();
          }
        });
      }
      
      console.log(`: ${audioName}`);
    } catch (error) {
      console.warn(`: ${audioName}`, error);
      // 
      this.currentlyPlaying = null;
    }
  }

  /**
   * Android
   */
  private async playWithRetry(audio: HTMLAudioElement, audioName: string, maxRetries: number = 3): Promise<void> {
    for (let i = 0; i < maxRetries; i++) {
      try {
        await audio.play();
        
        // 
        await new Promise<void>((resolve) => {
          const onEnded = () => {
            audio.removeEventListener('ended', onEnded);
            // 
            if (this.currentlyPlaying === audio) {
              this.currentlyPlaying = null;
            }
            resolve();
          };
          audio.addEventListener('ended', onEnded);
          
          // resolve
          if (audio.ended) {
            if (this.currentlyPlaying === audio) {
              this.currentlyPlaying = null;
            }
            resolve();
          }
        });
        
        return; // 
      } catch (error) {
        console.warn(` ${i + 1}/${maxRetries}: ${audioName}`, error);
        
        if (i < maxRetries - 1) {
          // 
          await this.delay(200 * (i + 1));
          
          // 
          audio.load();
        } else {
          throw error; // 
        }
      }
    }
  }

  /**
   * 
   */
  public async playNumber(num: number): Promise<void> {
    if (!this.isEnabled) {
      return;
    }

    // 0-10
    if (Number.isInteger(num) && num >= 0 && num <= 10) {
      await this.playAudio(`num${num}`);
      return;
    }

    // 
    await this.playChineseNumber(num);
  }

  /**
   * 
   */
  public async playEnglishNumber(num: number): Promise<void> {
    if (!this.isEnabled) {
      return;
    }

    const englishNumberMap: Record<number, string> = {
      0: 'zero',
      1: 'one',
      2: 'two',
      3: 'three',
      4: 'four',
      5: 'five',
      6: 'six',
      7: 'seven',
      8: 'eight',
      9: 'nine'
    };

    const audioName = englishNumberMap[num];
    if (audioName) {
      await this.playAudio(audioName);
    }
  }

  /**
   * 
   */
  private async playChineseNumber(num: number): Promise<void> {
    const isCompositeNumber = num >= 11; // 11
    const playbackRate = isCompositeNumber ? 1.2 : 1.0; // 1.2
    if (num < 0) {
      await this.playAudio('fu', playbackRate);
      await this.delay(400);
      await this.playChineseNumber(-num);
      return;
    }

    if (num % 1 !== 0) {
      // 
      const parts = num.toString().split('.');
      await this.playChineseNumber(parseInt(parts[0]));
      await this.delay(400);
      await this.playAudio('dian', playbackRate);
      await this.delay(400);
      for (const digit of parts[1]) {
        await this.playAudio(`num${digit}`, playbackRate);
        await this.delay(300);
      }
      return;
    }

    // 
    if (num === 0) {
      await this.playAudio('num0', playbackRate);
      return;
    }

    if (num >= 1 && num <= 10) {
      await this.playAudio(`num${num}`, playbackRate);
      return;
    }

    if (num >= 11 && num <= 19) {
      // 
      await this.playAudio('shi', playbackRate);
      await this.delay(400);
      if (num > 10) {
        await this.playAudio(`num${num - 10}`, playbackRate);
      }
      return;
    }

    if (num >= 20 && num <= 99) {
      // 
      const tens = Math.floor(num / 10);
      const ones = num % 10;
      
      await this.playAudio(`num${tens}`, playbackRate);
      await this.delay(400);
      await this.playAudio('shi', playbackRate);
      
      if (ones > 0) {
        await this.delay(400);
        await this.playAudio(`num${ones}`, playbackRate);
      }
      return;
    }

    if (num >= 100 && num <= 999) {
      // 
      const hundreds = Math.floor(num / 100);
      const remainder = num % 100;
      
      await this.playAudio(`num${hundreds}`, playbackRate);
      await this.delay(400);
      await this.playAudio('bai', playbackRate);
      
      if (remainder > 0) {
        await this.delay(400);
        if (remainder < 10) {
          await this.playAudio('num0', playbackRate); // ""
          await this.delay(400);
          await this.playAudio(`num${remainder}`, playbackRate);
        } else if (remainder >= 10 && remainder <= 19) {
          // 10-19"X""X"
          await this.playAudio('num1', playbackRate);
          await this.delay(400);
          await this.playAudio('shi', playbackRate);
          if (remainder > 10) {
            await this.delay(400);
            await this.playAudio(`num${remainder - 10}`, playbackRate);
          }
        } else {
          // 20-99
          await this.playChineseNumber(remainder);
        }
      }
      return;
    }

    if (num >= 1000 && num <= 9999) {
      // 
      const thousands = Math.floor(num / 1000);
      const remainder = num % 1000;
      
      await this.playAudio(`num${thousands}`, playbackRate);
      await this.delay(400);
      await this.playAudio('qian', playbackRate);
      
      if (remainder > 0) {
        await this.delay(400);
        if (remainder < 100) {
          await this.playAudio('num0', playbackRate); // ""
          await this.delay(400);
        }
        await this.playChineseNumber(remainder);
      }
      return;
    }

    // 
    await this.playComplexNumber(num);
  }

  /**
   * 
   */
  private async playComplexNumber(num: number): Promise<void> {
    const numStr = num.toString();
    
    for (let i = 0; i < numStr.length; i++) {
      const char = numStr[i];
      
      if (char === '-') {
        await this.playAudio('fu');
      } else if (char === '.') {
        await this.playAudio('dian');
      } else if (char >= '0' && char <= '9') {
        await this.playAudio(`num${char}`);
      }
      
      if (i < numStr.length - 1) {
        await this.delay(400);
      }
    }
  }

  /**
   * 
   */
  private delay(ms: number): Promise<void> {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  /**
   * 
   */
  public async playOperator(operator: string): Promise<void> {
    const operatorMap: Record<string, string> = {
      '+': 'jia',
      '-': 'jian', 
      '*': 'chengyi',
      '': 'chengyi',
      '/': 'chuyi',
      '': 'chuyi',
      // /
      'add': 'jia',
      'plus': 'jia',
      'addition': 'jia',
      'subtract': 'jian',
      'minus': 'jian',
      'subtraction': 'jian',
      'multiply': 'chengyi',
      'times': 'chengyi',
      'multiple': 'chengyi',
      'divide': 'chuyi',
      'division': 'chuyi',
      '=': 'dengyu',
      'equals': 'dengyu',
      '.': 'dian',
      '%': 'baifenhao',
      // 
      'equal': 'dengyu',
      'dot': 'dot',
      'percent': 'percent',
      'minus_sign': 'minus_sign'
    };

    const audioName = operatorMap[operator];
    console.log(` [DEBUG] : ${operator} -> ${audioName}`);
    console.log(` [DEBUG] : ${this.isEnabled}`);
    console.log(` [DEBUG] : ${this.currentVoiceProfile}`);
    
    if (audioName) {
      console.log(` [DEBUG] : ${audioName}`);
      // 
      const cachedAudio = this.audioCache.get(audioName);
      if (cachedAudio) {
        console.log(` [DEBUG]  - readyState: ${cachedAudio.readyState}, networkState: ${cachedAudio.networkState}, error: ${cachedAudio.error}`);
      } else {
        console.log(` [DEBUG] : ${audioName}`);
      }
      
      try {
        await this.playAudio(audioName);
        console.log(` [DEBUG] : ${audioName}`);
      } catch (error) {
        console.error(` [ERROR] : ${audioName}`, error);
        // 
        this.audioCache.delete(audioName);
        console.log(` [DEBUG] : ${audioName}`);
        throw error;
      }
    } else {
      console.warn(` [WARN] : ${operator}`);
    }
  }

  /**
   * 
   */
  public async playChineseUnit(unit: string): Promise<void> {
    const unitMap: Record<string, string> = {
      '': 'shi',
      '': 'bai', 
      '': 'qian',
      '': 'wan',
      '': 'yi'
    };

    const audioName = unitMap[unit];
    if (audioName) {
      await this.playAudio(audioName);
    }
  }

  /**
   * 
   */
  public async playDelete(): Promise<void> {
    await this.playAudio('del');
  }

  /**
   * 
   */
  public setEnabled(enabled: boolean): void {
    this.isEnabled = enabled;
  }

  /**
   * 
   */
  public getEnabled(): boolean {
    return this.isEnabled;
  }

  /**
   * 
   */
  public preloadCommonAudios(): void {
    // 0-10
    for (let i = 0; i <= 10; i++) {
      this.preloadAudio(`num${i}`);
    }

    // 0-9
    const englishNumbers = ['zero', 'one', 'two', 'three', 'four', 'five', 'six', 'seven', 'eight', 'nine'];
    englishNumbers.forEach(num => this.preloadAudio(num));

    // 
    const chineseOperators = ['jia', 'jian', 'chengyi', 'chuyi', 'dengyu', 'dian', 'baifenhao', 'fu'];
    chineseOperators.forEach(op => this.preloadAudio(op));

    // 
    const englishOperators = ['add', 'subtraction', 'multiple', 'division', 'equal', 'dot', 'percent', 'minus_sign'];
    englishOperators.forEach(op => this.preloadAudio(op));

    // 
    const chineseUnits = ['shi', 'bai', 'qian', 'wan', 'yi'];
    chineseUnits.forEach(unit => this.preloadAudio(unit));

    // 
    this.preloadAudio('del');
    this.preloadAudio('fenhao');
    this.preloadAudio('e');
    this.preloadAudio('shuzibai');
  }
}

// 
export const audioPlayer = AudioPlayer.getInstance();