import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { ArrowLeft, Star, Trophy, Target, Zap } from 'lucide-react';
import GameChallenge from '@/components/GameChallenge';
import AchievementNotification from '@/components/AchievementNotification';
import GameCharacter from '@/components/GameCharacter';
import GameStorage, { type LevelProgress, type Achievement } from '@/lib/gameStorage';

// 
type GameState = 'levelSelect' | 'playing' | 'achievements';

// GameChallenge
interface LevelInfo {
  id: number;
  name: string;
  difficulty: 'easy' | 'medium' | 'hard';
  timeLimit: number;
  questionsCount: number;
  starsToUnlock: number;
}

// 
interface LevelDisplay {
  id: number;
  name: string;
  difficulty: 'easy' | 'medium' | 'hard';
  unlocked: boolean;
  completed: boolean;
  stars: number;
  maxStars: number;
  difficultyLevel: number;
}

export default function Game() {
  const navigate = useNavigate();
  const [gameState, setGameState] = useState<GameState>('levelSelect');
  const [currentLevel, setCurrentLevel] = useState<LevelDisplay | null>(null);
  const [levels, setLevels] = useState<LevelDisplay[]>([]);
  const [achievements, setAchievements] = useState<Achievement[]>([]);
  const [newAchievement, setNewAchievement] = useState<Achievement | null>(null);
  const [characterEmotion, setCharacterEmotion] = useState<'happy' | 'excited' | 'thinking' | 'sad' | 'celebrating'>('happy');
  const [characterMessage, setCharacterMessage] = useState<string>('');
  const [showCharacterMessage, setShowCharacterMessage] = useState(true);
  
  // 
  const levelTemplates = [
    { id: 1, name: '', difficulty: 'easy' as const, difficultyLevel: 1 },
    { id: 2, name: '', difficulty: 'easy' as const, difficultyLevel: 1 },
    { id: 3, name: '', difficulty: 'easy' as const, difficultyLevel: 1 },
    { id: 4, name: '', difficulty: 'medium' as const, difficultyLevel: 2 },
    { id: 5, name: '', difficulty: 'medium' as const, difficultyLevel: 2 },
    { id: 6, name: '', difficulty: 'hard' as const, difficultyLevel: 3 },
  ];
  
  // 
  useEffect(() => {
    const loadGameData = () => {
      const levelProgress = GameStorage.getAllLevels();
      const achievementData = GameStorage.getAchievements();
      
      // 
      const levelDisplayData: LevelDisplay[] = levelTemplates.map(template => {
        const progress = levelProgress.find(p => p.id === template.id);
        return {
          ...template,
          unlocked: progress?.unlocked || false,
          completed: progress?.completed || false,
          stars: progress?.stars || 0,
          maxStars: 3,
          difficultyLevel: template.difficultyLevel,
        };
      });
      
      setLevels(levelDisplayData);
      setAchievements(achievementData);
    };
    
    loadGameData();
    
    // 
    const unsubscribe = GameStorage.onAchievementUnlocked((achievement) => {
      setNewAchievement(achievement);
    });
    
    return unsubscribe;
  }, []);

  const handleBackToHome = () => {
    navigate('/');
  };

  const handleLevelClick = (level: LevelDisplay) => {
    if (level.unlocked) {
      setCharacterEmotion('excited');
      setCharacterMessage('');
      setShowCharacterMessage(true);
      
      // 
      setTimeout(() => {
        setCurrentLevel(level);
        setGameState('playing');
      }, 300);
    } else {
      setCharacterEmotion('thinking');
      setCharacterMessage('~');
      setShowCharacterMessage(true);
      
      setTimeout(() => {
        setCharacterEmotion('happy');
        setCharacterMessage('');
      }, 2000);
    }
  };

  const handleGameComplete = (timeUsed: number) => {
    if (currentLevel) {
      // 
      const timeLimit = getLevelInfo(currentLevel).timeLimit;
      let stars = 1;
      if (timeUsed <= timeLimit * 0.5) stars = 3;
      else if (timeUsed <= timeLimit * 0.75) stars = 2;
      
      // 
      const coins = stars * 50 + Math.max(0, Math.floor((timeLimit - timeUsed) / 10));
      
      // 
      GameStorage.updateLevelProgress(
        currentLevel.id,
        true,
        stars,
        timeUsed,
        coins
      );
      
      console.log(` ${currentLevel.name}  ${stars} ${coins} `);
      
      // 
      const levelProgress = GameStorage.getAllLevels();
      const achievementData = GameStorage.getAchievements();
      
      const levelDisplayData: LevelDisplay[] = levelTemplates.map(template => {
        const progress = levelProgress.find(p => p.id === template.id);
        return {
          ...template,
          unlocked: progress?.unlocked || false,
          completed: progress?.completed || false,
          stars: progress?.stars || 0,
          maxStars: 3,
          difficultyLevel: template.difficultyLevel,
        };
      });
      
      setLevels(levelDisplayData);
      setAchievements(achievementData);
      
      // 
      setCharacterEmotion('celebrating');
      setCharacterMessage('');
      setShowCharacterMessage(true);
      
      // 
      setGameState('levelSelect');
      setCurrentLevel(null);
    }
    
    // 
    setTimeout(() => {
      setCharacterEmotion('happy');
      setCharacterMessage('');
    }, 3000);
  };

  const handleGameExit = () => {
    setCharacterEmotion('thinking');
    setCharacterMessage('');
    setShowCharacterMessage(true);
    
    setGameState('levelSelect');
    setCurrentLevel(null);
    
    setTimeout(() => {
      setCharacterEmotion('happy');
      setCharacterMessage('');
    }, 2000);
  };

  // LevelDisplayLevelInfo
  const getLevelInfo = (level: LevelDisplay): LevelInfo => {
    const difficultyMap = {
      easy: { timeLimit: 120, questionsCount: 5 },
      medium: { timeLimit: 90, questionsCount: 8 },
      hard: { timeLimit: 60, questionsCount: 10 }
    };
    
    const config = difficultyMap[level.difficulty];
    
    return {
      id: level.id,
      name: level.name,
      difficulty: level.difficulty,
      timeLimit: config.timeLimit,
      questionsCount: config.questionsCount,
      starsToUnlock: 1
    };
  };

  const renderLevelSelect = () => (
    <div className="min-h-screen bg-gradient-to-br from-purple-400 via-pink-400 to-orange-400 p-4">
      {/*  */}
      <div className="flex items-center justify-between mb-6">
        <motion.button
          onClick={handleBackToHome}
          className="flex items-center gap-2 bg-white/20 backdrop-blur-sm rounded-full px-4 py-2 text-white font-bold"
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
        >
          <ArrowLeft className="w-5 h-5" />
          
        </motion.button>
        
        <h1 className="text-2xl font-bold text-white"></h1>
        
        <motion.button
          onClick={() => setGameState('achievements')}
          className="flex items-center gap-2 bg-yellow-400 rounded-full px-4 py-2 text-yellow-900 font-bold"
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
        >
          <Trophy className="w-5 h-5" />
          
        </motion.button>
      </div>

      {/*  */}
      <motion.div
        initial={{ y: -50, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        className="text-center mb-8"
      >
        <p className="text-white/80 text-lg mb-6"></p>
        
        {/*  */}
        <div className="mt-6">
          <GameCharacter
            emotion={characterEmotion}
            size="large"
            position="center"
            message={characterMessage}
            showMessage={showCharacterMessage}
          />
        </div>
      </motion.div>

      {/*  */}
      <div className="grid grid-cols-2 md:grid-cols-3 gap-4 max-w-4xl mx-auto">
        {levels.map((level, index) => (
          <motion.div
            key={level.id}
            initial={{ scale: 0, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            transition={{ delay: index * 0.1 }}
            className={`relative ${
              level.unlocked ? 'cursor-pointer' : 'cursor-not-allowed opacity-50'
            }`}
            onClick={() => handleLevelClick(level)}
          >
            <motion.div
              className={`
                relative p-6 rounded-3xl text-center
                ${level.unlocked 
                  ? level.completed 
                    ? 'bg-gradient-to-br from-green-400 to-emerald-500 text-white'
                    : 'bg-gradient-to-br from-blue-400 to-cyan-500 text-white'
                  : 'bg-gray-300 text-gray-500'
                }
                shadow-lg border-4 border-white/30
              `}
              whileHover={level.unlocked ? { scale: 1.05, y: -5 } : {}}
              whileTap={level.unlocked ? { scale: 0.95 } : {}}
            >
              {/*  */}
              <div className="text-3xl font-bold mb-2">{level.id}</div>
              
              {/*  */}
              <div className="text-sm font-semibold mb-3">{level.name}</div>
              
              {/*  */}
              <div className="flex justify-center items-center gap-2 mb-2">
                <div className="flex gap-1">
                  {Array.from({ length: level.maxStars }).map((_, i) => (
                    <Star
                      key={i}
                      className={`w-4 h-4 ${
                        i < level.stars ? 'text-yellow-300 fill-yellow-300' : 'text-white/30'
                      }`}
                    />
                  ))}
                </div>
                <div className="bg-white/20 rounded-full px-2 py-1">
                  <span className="text-white text-xs font-bold">L{level.difficultyLevel}</span>
                </div>
              </div>
              
              {/*  */}
              <div className={`
                inline-block px-2 py-1 rounded-full text-xs font-bold
                ${level.difficulty === 'easy' ? 'bg-green-200 text-green-800' :
                  level.difficulty === 'medium' ? 'bg-yellow-200 text-yellow-800' :
                  'bg-red-200 text-red-800'
                }
              `}>
                {level.difficulty === 'easy' ? '' :
                 level.difficulty === 'medium' ? '' : ''}
              </div>
              
              {/*  */}
              {!level.unlocked && (
                <div className="absolute inset-0 flex items-center justify-center bg-black/20 rounded-3xl">
                  <div className="text-4xl"></div>
                </div>
              )}
            </motion.div>
          </motion.div>
        ))}
      </div>
    </div>
  );

  const renderAchievements = () => (
    <div className="min-h-screen bg-gradient-to-br from-indigo-400 via-purple-400 to-pink-400 p-4">
      {/*  */}
      <div className="flex items-center justify-between mb-6">
        <motion.button
          onClick={() => setGameState('levelSelect')}
          className="flex items-center gap-2 bg-white/20 backdrop-blur-sm rounded-full px-4 py-2 text-white font-bold"
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
        >
          <ArrowLeft className="w-5 h-5" />
          
        </motion.button>
        
        <h1 className="text-2xl font-bold text-white"></h1>
        
        <div className="w-20"></div>
      </div>

      {/*  */}
      <motion.div
        initial={{ y: -50, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        className="text-center mb-8"
      >
        <p className="text-white/80 text-lg"></p>
      </motion.div>

      {/*  */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 max-w-4xl mx-auto">
        {achievements.map((achievement, index) => (
          <motion.div
            key={achievement.id}
            initial={{ x: -50, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            transition={{ delay: index * 0.1 }}
            className={`
              p-6 rounded-3xl border-4 border-white/30 shadow-lg
              ${achievement.unlocked 
                ? 'bg-gradient-to-br from-yellow-400 to-orange-500 text-white'
                : 'bg-gray-300 text-gray-500 opacity-60'
              }
            `}
          >
            <div className="flex items-center gap-4">
              <div className="text-4xl">{achievement.icon}</div>
              <div className="flex-1">
                <h3 className="text-xl font-bold mb-1">{achievement.name}</h3>
                <p className="text-sm opacity-80">{achievement.description}</p>
              </div>
              {achievement.unlocked && (
                <div className="text-2xl"></div>
              )}
            </div>
          </motion.div>
        ))}
      </div>
    </div>
  );

  return (
    <>
      {/*  */}
      {(() => {
        switch (gameState) {
          case 'levelSelect':
            return renderLevelSelect();
          case 'playing':
            return currentLevel ? (
              <GameChallenge
                level={getLevelInfo(currentLevel)}
                onComplete={handleGameComplete}
                onExit={handleGameExit}
              />
            ) : renderLevelSelect();
          case 'achievements':
            return renderAchievements();
          default:
            return renderLevelSelect();
        }
      })()}
      
      {/*  */}
      <AchievementNotification
        achievement={newAchievement}
        onClose={() => setNewAchievement(null)}
      />
    </>
  );
}