import { useEffect } from "react";
import { BrowserRouter as Router, Routes, Route, useLocation, useNavigate } from "react-router-dom";
import { App as CapacitorApp } from '@capacitor/app';
import Home from "@/pages/Home";
import Calculator from "@/pages/Calculator";
import Practice from "@/pages/Practice";
import Game from "@/pages/Game";
import Report from "@/pages/Report";
import Settings from "@/pages/Settings";
import VoiceSelect from "@/pages/VoiceSelect";
import VisualMath from "@/pages/VisualMath";
import PrivacyPolicy from "@/pages/PrivacyPolicy";
import UserAgreement from "@/pages/UserAgreement";

// 
function AppContent() {
  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    let listenerHandle: any = null;

    const setupListener = async () => {
      const handleBackButton = (info: any) => {
        console.log(':', location.pathname);
        
        // 
        if (location.pathname === '/') {
          console.log('');
          // 
        } else {
          console.log('');
          navigate(-1); // 
        }
      };

      // 
      listenerHandle = await CapacitorApp.addListener('backButton', handleBackButton);
    };

    setupListener();

    // 
    return () => {
      if (listenerHandle) {
        listenerHandle.remove();
      }
    };
  }, [location.pathname, navigate]);

  return (
    <Routes>
      <Route path="/" element={<Home />} />
      <Route path="/calculator" element={<Calculator />} />
      <Route path="/practice" element={<Practice />} />
      <Route path="/game" element={<Game />} />
      <Route path="/report" element={<Report />} />
      <Route path="/settings" element={<Settings />} />
      <Route path="/voice-select" element={<VoiceSelect />} />
      <Route path="/visual-math" element={<VisualMath />} />
      <Route path="/privacy-policy" element={<PrivacyPolicy />} />
      <Route path="/user-agreement" element={<UserAgreement />} />
    </Routes>
  );
}

export default function App() {
  return (
    <Router>
      <AppContent />
    </Router>
  );
}
