import { useState, useEffect, useCallback, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ArrowLeft, Clock, Star, Coins, Heart, Zap } from 'lucide-react';
import { useAudio } from '@/hooks/useAudio';
import GameCharacter from './GameCharacter';

// 
interface Question {
  id: number;
  question: string;
  options: number[];
  correctAnswer: number;
  difficulty: 'easy' | 'medium' | 'hard';
}

// 
interface LevelInfo {
  id: number;
  name: string;
  difficulty: 'easy' | 'medium' | 'hard';
  timeLimit: number; // 
  questionsCount: number;
  starsToUnlock: number;
}

interface GameChallengeProps {
  level: LevelInfo;
  onComplete: (stars: number, coins: number, timeUsed: number) => void;
  onExit: () => void;
}

export default function GameChallenge({ level, onComplete, onExit }: GameChallengeProps) {
  const { playNumber, playOperator, playCorrect, playWrong, playButtonClick } = useAudio();
  
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [score, setScore] = useState(0);
  const [timeLeft, setTimeLeft] = useState(level.timeLimit);
  const [gameState, setGameState] = useState<'ready' | 'playing' | 'completed' | 'failed'>('ready');
  const [selectedAnswer, setSelectedAnswer] = useState<number | null>(null);
  const [showResult, setShowResult] = useState(false);
  const [hearts, setHearts] = useState(() => {
    // 
    const difficultyLevel = Math.min(5, Math.floor((level.id - 1) / 3) + 1);
    return Math.max(1, 4 - Math.floor(difficultyLevel / 2));
  }); // 
  const [coins, setCoins] = useState(0);
  const [combo, setCombo] = useState(0); // 
  const [characterEmotion, setCharacterEmotion] = useState<'happy' | 'excited' | 'thinking' | 'sad' | 'celebrating'>('thinking');
  const [characterMessage, setCharacterMessage] = useState<string>('');
  const [showCharacterMessage, setShowCharacterMessage] = useState(false);
  // /
  const speechSeqRef = useRef(0);
  const speakTimerRef = useRef<number | null>(null);
  
  // 
  const generateQuestions = useCallback((): Question[] => {
    const questions: Question[] = [];
    
    // ID
    const difficultyLevel = Math.min(5, Math.floor((level.id - 1) / 3) + 1);
    const difficultyConfig = {
      1: { maxNum: 10, operators: ['+', '-'], hasNegative: false, complexity: 'simple' },
      2: { maxNum: 20, operators: ['+', '-'], hasNegative: false, complexity: 'simple' },
      3: { maxNum: 50, operators: ['+', '-', '*'], hasNegative: true, complexity: 'medium' },
      4: { maxNum: 100, operators: ['+', '-', '*'], hasNegative: true, complexity: 'medium' },
      5: { maxNum: 100, operators: ['+', '-', '*', '/'], hasNegative: true, complexity: 'complex' }
    };
    
    const config = difficultyConfig[difficultyLevel as keyof typeof difficultyConfig] || difficultyConfig[1];
    
    for (let i = 0; i < level.questionsCount; i++) {
      let question: Question;
      
      if (config.complexity === 'simple') {
        // 
        const a = Math.floor(Math.random() * config.maxNum) + 1;
        const b = Math.floor(Math.random() * config.maxNum) + 1;
        const isAddition = Math.random() > 0.5;
        
        if (isAddition) {
          const answer = a + b;
          question = {
            id: i + 1,
            question: `${a} + ${b} = ?`,
            options: [answer, answer + 1, answer - 1, answer + 2].sort(() => Math.random() - 0.5),
            correctAnswer: answer,
            difficulty: level.difficulty
          };
        } else {
          const answer = Math.max(a, b) - Math.min(a, b);
          question = {
            id: i + 1,
            question: `${Math.max(a, b)} - ${Math.min(a, b)} = ?`,
            options: [answer, answer + 1, answer - 1, answer + 2].filter(x => x >= 0).sort(() => Math.random() - 0.5),
            correctAnswer: answer,
            difficulty: level.difficulty
          };
        }
      } else if (config.complexity === 'medium') {
        // 
        const operator = config.operators[Math.floor(Math.random() * config.operators.length)];
        
        if (operator === '*') {
          const a = Math.floor(Math.random() * 12) + 1;
          const b = Math.floor(Math.random() * 12) + 1;
          const answer = a * b;
          question = {
            id: i + 1,
            question: `${a}  ${b} = ?`,
            options: [answer, answer + a, answer - a, answer + b].sort(() => Math.random() - 0.5),
            correctAnswer: answer,
            difficulty: level.difficulty
          };
        } else {
          const num1 = Math.floor(Math.random() * config.maxNum) + 10;
          const num2 = Math.floor(Math.random() * (config.maxNum / 2)) + 5;
          const answer = operator === '+' ? num1 + num2 : num1 - num2;
          question = {
            id: i + 1,
            question: `${num1} ${operator} ${num2} = ?`,
            options: [answer, answer + 5, answer - 5, answer + 10].filter(x => config.hasNegative || x >= 0).sort(() => Math.random() - 0.5),
            correctAnswer: answer,
            difficulty: level.difficulty
          };
        }
      } else {
        // 
        const a = Math.floor(Math.random() * 15) + 5;
        const b = Math.floor(Math.random() * 10) + 2;
        const c = Math.floor(Math.random() * 8) + 1;
        const answer = a + b * c;
        question = {
          id: i + 1,
          question: `${a} + ${b}  ${c} = ?`,
          options: [answer, (a + b) * c, a * b + c, answer + 5].sort(() => Math.random() - 0.5),
          correctAnswer: answer,
          difficulty: level.difficulty
        };
      }
      
      questions.push(question);
    }
    
    return questions;
  }, [level]);
  
  const [questions] = useState<Question[]>(generateQuestions());
  const currentQuestion = questions[currentQuestionIndex];

  // 
  const speakQuestion = async (question: string) => {
    // 
    const mySeq = ++speechSeqRef.current;
    // 
    const parts = question.split(' ');
    for (let i = 0; i < parts.length; i++) {
      // 
      if (speechSeqRef.current !== mySeq) return;
      const part = parts[i].trim();
      if (part === '') continue;
      
      if (!isNaN(Number(part))) {
        // 
        await playNumber(Number(part));
      } else if (['+', '-', '', '', '=', '*', '/'].includes(part)) {
        // 
        await playOperator(part);
      } else if (part === '?') {
        // "?"
        await new Promise(resolve => setTimeout(resolve, 200));
      }
      
      // 
      if (i < parts.length - 1) {
        await new Promise(resolve => setTimeout(resolve, 300));
      }
    }
  };
  
  // 
  useEffect(() => {
    if (gameState !== 'playing') return;
    
    const timer = setInterval(() => {
      setTimeLeft(prev => {
        if (prev <= 1) {
          setGameState('failed');
          return 0;
        }
        return prev - 1;
      });
    }, 1000);
    
    return () => clearInterval(timer);
  }, [gameState]);
  
  // 
  const handleStartGame = () => {
    setGameState('playing');
    setCharacterEmotion('excited');
    setCharacterMessage('');
    setShowCharacterMessage(true);
    
    setTimeout(() => {
      setCharacterEmotion('thinking');
      setShowCharacterMessage(false);
    }, 1500);
  };

  // 
  useEffect(() => {
    if (gameState === 'playing' && currentQuestion) {
      // 
      if (speakTimerRef.current) {
        clearTimeout(speakTimerRef.current);
        speakTimerRef.current = null;
      }
      // 
      const t = window.setTimeout(() => {
        speakQuestion(currentQuestion.question);
      }, 600);
      speakTimerRef.current = t as unknown as number;
      return () => {
        if (speakTimerRef.current) {
          clearTimeout(speakTimerRef.current);
          speakTimerRef.current = null;
        }
        // 
        speechSeqRef.current++;
      };
    } else {
      // playing
      speechSeqRef.current++;
      if (speakTimerRef.current) {
        clearTimeout(speakTimerRef.current);
        speakTimerRef.current = null;
      }
    }
  }, [currentQuestionIndex, gameState, currentQuestion]);
  
  const handleAnswerSelect = (answer: number) => {
    if (selectedAnswer !== null || showResult) return;
    
    // 
    speechSeqRef.current++;
    if (speakTimerRef.current) {
      clearTimeout(speakTimerRef.current);
      speakTimerRef.current = null;
    }

    playButtonClick();
    setSelectedAnswer(answer);
    setShowResult(true);
    
    setTimeout(() => {
      const isCorrect = answer === currentQuestion.correctAnswer;
      
      if (isCorrect) {
          playCorrect();
          setScore(prev => prev + 1);
          setCombo(prev => prev + 1);
          
          // 
          if (combo >= 2) {
            setCharacterEmotion('excited');
            setCharacterMessage(` x${combo + 1}`);
          } else {
            setCharacterEmotion('happy');
            setCharacterMessage('');
          }
          setShowCharacterMessage(true);
          
          // 
          const coinReward = 10 + combo * 2;
          setCoins(prev => prev + coinReward);
        
        // 
        if (currentQuestionIndex < questions.length - 1) {
          setTimeout(() => {
          setCurrentQuestionIndex(prev => prev + 1);
          setSelectedAnswer(null);
          setShowResult(false);
          setCharacterEmotion('thinking');
          setShowCharacterMessage(false);
        }, 1500);
        } else {
          setTimeout(() => {
            setCharacterEmotion('celebrating');
            setCharacterMessage('');
            setShowCharacterMessage(true);
            setTimeout(() => {
              setGameState('completed');
            }, 1000);
          }, 1500);
        }
      } else {
          playWrong();
          setCombo(0);
          
          // 
          setCharacterEmotion('sad');
          setCharacterMessage('...');
          setShowCharacterMessage(true);
          
          setHearts(prev => {
            const newHearts = prev - 1;
            if (newHearts <= 0) {
              setGameState('failed');
            } else {
            // 
            setTimeout(() => {
                if (currentQuestionIndex < questions.length - 1) {
                  setCurrentQuestionIndex(prev => prev + 1);
                  setSelectedAnswer(null);
                  setShowResult(false);
                  setCharacterEmotion('thinking');
                  setShowCharacterMessage(false);
                } else {
                  setGameState('completed');
                }
              }, 1500);
          }
          return newHearts;
        });
      }
    }, 500);
  };
  
  const calculateStars = () => {
    const accuracy = score / questions.length;
    const timeBonus = timeLeft > level.timeLimit * 0.5 ? 1 : 0;
    
    if (accuracy >= 0.9 && timeBonus) return 3;
    if (accuracy >= 0.7) return 2;
    if (accuracy >= 0.5) return 1;
    return 0;
  };
  
  const handleComplete = () => {
    const stars = calculateStars();
    const timeUsed = level.timeLimit - timeLeft;
    onComplete(stars, coins, timeUsed);
  };
  
  const getAnswerButtonStyle = (answer: number) => {
    if (!showResult) {
      return 'bg-gradient-to-br from-blue-400 to-blue-600 text-white hover:from-blue-500 hover:to-blue-700';
    }
    
    if (answer === currentQuestion.correctAnswer) {
      return 'bg-gradient-to-br from-green-400 to-green-600 text-white';
    }
    
    if (answer === selectedAnswer && answer !== currentQuestion.correctAnswer) {
      return 'bg-gradient-to-br from-red-400 to-red-600 text-white';
    }
    
    return 'bg-gray-300 text-gray-500';
  };
  
  if (gameState === 'ready') {
    return (
      <div className="min-h-screen bg-gradient-to-br from-indigo-400 via-purple-400 to-pink-400 p-4 flex items-center justify-center">
        <motion.div
          initial={{ scale: 0.5, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          className="bg-white rounded-3xl p-8 text-center shadow-2xl max-w-md w-full"
        >
          <div className="text-6xl mb-4"></div>
          <h2 className="text-3xl font-bold text-gray-800 mb-4">{level.name}</h2>
          <p className="text-gray-600 mb-6">
            <br />
             {level.timeLimit}  {level.questionsCount} 
          </p>
          
          <div className="mb-6">
            <div className="grid grid-cols-2 gap-4 text-sm">
              <div className="bg-blue-100 rounded-lg p-3">
                <div className="text-blue-600 font-bold"></div>
                <div className="text-2xl font-bold text-blue-800 capitalize">
                  {level.difficulty === 'easy' ? '' : level.difficulty === 'medium' ? '' : ''}
                </div>
              </div>
              
              <div className="bg-yellow-100 rounded-lg p-3">
                <div className="text-yellow-600 font-bold"></div>
                <div className="text-2xl font-bold text-yellow-800">
                  {level.timeLimit}s
                </div>
              </div>
            </div>
          </div>
          
          <div className="flex gap-4">
            <motion.button
              onClick={onExit}
              className="flex-1 bg-gray-400 text-white font-bold py-3 px-6 rounded-xl"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              
            </motion.button>
            
            <motion.button
              onClick={handleStartGame}
              className="flex-1 bg-gradient-to-r from-green-400 to-blue-500 text-white font-bold py-3 px-6 rounded-xl text-lg"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              
            </motion.button>
          </div>
        </motion.div>
      </div>
    );
  }

  if (gameState === 'completed') {
    const stars = calculateStars();
    return (
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        className="min-h-screen bg-gradient-to-br from-yellow-400 via-orange-400 to-pink-400 flex items-center justify-center p-4"
      >
        <motion.div
          initial={{ scale: 0.5, y: 50 }}
          animate={{ scale: 1, y: 0 }}
          className="bg-white rounded-3xl p-8 text-center shadow-2xl max-w-md w-full"
        >
          <div className="text-6xl mb-4"></div>
          <h2 className="text-3xl font-bold text-gray-800 mb-4"></h2>
          
          <div className="mb-6">
            <div className="flex justify-center gap-2 mb-4">
              {Array.from({ length: 3 }).map((_, i) => (
                <Star
                  key={i}
                  className={`w-8 h-8 ${
                    i < stars ? 'text-yellow-400 fill-yellow-400' : 'text-gray-300'
                  }`}
                />
              ))}
            </div>
            
            <div className="grid grid-cols-2 gap-4 text-sm">
              <div className="bg-blue-100 rounded-lg p-3">
                <div className="text-blue-600 font-bold"></div>
                <div className="text-2xl font-bold text-blue-800">
                  {Math.round((score / questions.length) * 100)}%
                </div>
              </div>
              
              <div className="bg-yellow-100 rounded-lg p-3">
                <div className="text-yellow-600 font-bold"></div>
                <div className="text-2xl font-bold text-yellow-800 flex items-center justify-center gap-1">
                  <Coins className="w-5 h-5" />
                  {coins}
                </div>
              </div>
            </div>
          </div>
          
          <motion.button
            onClick={handleComplete}
            className="w-full bg-gradient-to-r from-green-400 to-blue-500 text-white font-bold py-3 px-6 rounded-xl text-lg"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            
          </motion.button>
        </motion.div>
      </motion.div>
    );
  }
  
  if (gameState === 'failed') {
    return (
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        className="min-h-screen bg-gradient-to-br from-red-400 via-pink-400 to-purple-400 flex items-center justify-center p-4"
      >
        <motion.div
          initial={{ scale: 0.5, y: 50 }}
          animate={{ scale: 1, y: 0 }}
          className="bg-white rounded-3xl p-8 text-center shadow-2xl max-w-md w-full"
        >
          <div className="text-6xl mb-4"></div>
          <h2 className="text-3xl font-bold text-gray-800 mb-4"></h2>
          <p className="text-gray-600 mb-6">
            {hearts <= 0 ? '' : ''}
            <br />
          </p>
          
          <div className="flex gap-4">
            <motion.button
              onClick={onExit}
              className="flex-1 bg-gray-400 text-white font-bold py-3 px-6 rounded-xl"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              
            </motion.button>
            
            <motion.button
              onClick={() => window.location.reload()}
              className="flex-1 bg-gradient-to-r from-blue-400 to-purple-500 text-white font-bold py-3 px-6 rounded-xl"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              
            </motion.button>
          </div>
        </motion.div>
      </motion.div>
    );
  }
  
  return (
    <div className="min-h-screen bg-gradient-to-br from-indigo-400 via-purple-400 to-pink-400 p-4">
      {/*  */}
      <div className="mb-6">
        {/*  */}
        <div className="flex items-center justify-between mb-3">
          <motion.button
            onClick={onExit}
            className="flex items-center gap-2 bg-white/20 backdrop-blur-sm rounded-full px-4 py-2 text-white font-bold"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <ArrowLeft className="w-5 h-5" />
            
          </motion.button>
          
          <div className="flex items-center gap-3">
            {/*  */}
            <div className="flex items-center gap-1">
              {Array.from({ length: Math.max(1, 4 - Math.floor((Math.min(5, Math.floor((level.id - 1) / 3) + 1)) / 2)) }).map((_, i) => (
                <Heart
                  key={i}
                  className={`w-6 h-6 ${
                    i < hearts ? 'text-red-400 fill-red-400' : 'text-white/30'
                  }`}
                />
              ))}
            </div>
            
            {/*  */}
            <div className="flex items-center gap-2 bg-green-400/80 backdrop-blur-sm rounded-full px-3 py-1">
              <span className="text-white font-bold text-sm">L{level.id}</span>
            </div>
            
            {/*  */}
            <div className="flex items-center gap-2 bg-yellow-400 rounded-full px-3 py-1">
              <Coins className="w-5 h-5 text-yellow-800" />
              <span className="text-yellow-800 font-bold">{coins}</span>
            </div>
          </div>
        </div>
        
        {/*  */}
        <div className="flex justify-center">
          <div className="flex items-center gap-2 bg-white/20 backdrop-blur-sm rounded-full px-4 py-2">
            <Clock className="w-5 h-5 text-white" />
            <span className="text-white font-bold text-lg">{timeLeft}s</span>
          </div>
        </div>
      </div>
      
      {/*  */}
      <div className="mb-6">
        <div className="bg-white/20 rounded-full h-3 overflow-hidden">
          <motion.div
            className="bg-gradient-to-r from-green-400 to-blue-500 h-full"
            initial={{ width: 0 }}
            animate={{ width: `${((currentQuestionIndex + 1) / questions.length) * 100}%` }}
            transition={{ duration: 0.5 }}
          />
        </div>
        <div className="text-center text-white mt-2">
           {currentQuestionIndex + 1}  /  {questions.length} 
        </div>
      </div>
      
      {/*  */}
      <AnimatePresence>
        {combo > 1 && (
          <motion.div
            initial={{ scale: 0, y: -20 }}
            animate={{ scale: 1, y: 0 }}
            exit={{ scale: 0, y: -20 }}
            className="text-center mb-4"
          >
            <div className="inline-flex items-center gap-2 bg-orange-400 text-white px-4 py-2 rounded-full font-bold">
              <Zap className="w-5 h-5" />
               x{combo}
            </div>
          </motion.div>
        )}
      </AnimatePresence>
      
      {/*  */}
      <div className="mb-4">
        <GameCharacter
          emotion={characterEmotion}
          size="medium"
          position="center"
          message={characterMessage}
          showMessage={showCharacterMessage}
        />
      </div>
      
      {/*  */}
      <motion.div
        key={currentQuestion.id}
        initial={{ x: 100, opacity: 0 }}
        animate={{ x: 0, opacity: 1 }}
        className="bg-white rounded-3xl p-8 mb-6 shadow-xl max-w-2xl mx-auto"
      >
        <h2 className="text-3xl font-bold text-center text-gray-800 mb-8">
          {currentQuestion.question}
        </h2>
        
        <div className="grid grid-cols-2 gap-4">
          {currentQuestion.options.map((option, index) => (
            <motion.button
              key={index}
              onClick={() => handleAnswerSelect(option)}
              disabled={showResult}
              className={`
                p-6 rounded-2xl text-2xl font-bold transition-all duration-300
                ${getAnswerButtonStyle(option)}
                ${!showResult ? 'hover:scale-105 active:scale-95' : ''}
                ${showResult ? 'cursor-not-allowed' : 'cursor-pointer'}
              `}
              whileHover={!showResult ? { scale: 1.05 } : {}}
              whileTap={!showResult ? { scale: 0.95 } : {}}
            >
              {option}
            </motion.button>
          ))}
        </div>
      </motion.div>
    </div>
  );
}