import React, { useState, useEffect, useCallback } from 'react';
import { ArrowLeft, RefreshCw, Star, Heart, Volume2 } from 'lucide-react';
import { useAudio } from '../../hooks/useAudio';
import StudyDataStorage from '../../lib/studyDataStorage';

interface DivisionPracticeProps {
  onBack: () => void;
}

interface Question {
  num1: number;
  num2: number;
  answer: number;
}

interface Stats {
  correct: number;
  total: number;
  stars: number;
}

const DivisionPractice: React.FC<DivisionPracticeProps> = ({ onBack }) => {
  const { playNumber, playOperator } = useAudio();
  const [question, setQuestion] = useState<Question>({ num1: 0, num2: 0, answer: 0 });
  const [userAnswer, setUserAnswer] = useState('');
  const [stats, setStats] = useState<Stats>({ correct: 0, total: 0, stars: 0 });
  const [showResult, setShowResult] = useState(false);
  const [isCorrect, setIsCorrect] = useState(false);
  const [showCelebration, setShowCelebration] = useState(false);
  const [feedback, setFeedback] = useState('');
  const [sessionId] = useState(() => StudyDataStorage.startSession('practice'));
  const [questionStartTime, setQuestionStartTime] = useState<number>(Date.now());
  const [isPlayingAudio, setIsPlayingAudio] = useState(false);

  // 
  const playQuestionAudio = useCallback(async (num1: number, num2: number) => {
    if (isPlayingAudio) return; // 
    
    setIsPlayingAudio(true);
    try {
      await new Promise(resolve => setTimeout(resolve, 500)); // 
      await playNumber(num1);
      await new Promise(resolve => setTimeout(resolve, 400)); // 
      //  'divide' | 'division' | '' | '/'
      await playOperator('division');
      await new Promise(resolve => setTimeout(resolve, 400)); // 
      await playNumber(num2);
    } catch (error) {
      console.error(':', error);
    } finally {
      setIsPlayingAudio(false);
    }
  }, [playNumber, playOperator, isPlayingAudio]);

  // 
  const replayQuestion = useCallback(() => {
    playQuestionAudio(question.num1, question.num2);
  }, [playQuestionAudio, question.num1, question.num2]);

  // 
  const generateQuestion = useCallback(() => {
    // 
    const divisors = [2, 3, 4, 5, 6, 7, 8, 9, 10]; // 
    const num2 = divisors[Math.floor(Math.random() * divisors.length)];
    const answer = Math.floor(Math.random() * 10) + 1; // 1-10
    const num1 = num2 * answer; //  =   
    
    setQuestion({ num1, num2, answer });
    setUserAnswer('');
    setShowResult(false);
    setShowCelebration(false);
    setFeedback('');
    setQuestionStartTime(Date.now());
    
    // 
    setTimeout(() => {
      playQuestionAudio(num1, num2);
    }, 600);
  }, [playQuestionAudio]);

  // 
  const playAnswerFeedback = useCallback(async (correct: boolean, answer: number, correctAnswer: number) => {
    setIsPlayingAudio(true);
    try {
      if (correct) {
        await new Promise(resolve => setTimeout(resolve, 300));
        await playNumber(answer);
        await new Promise(resolve => setTimeout(resolve, 2000));
        
        // 
        const num2 = Math.floor(Math.random() * 9) + 1;
        const quotient = Math.floor(Math.random() * 9) + 1;
        const num1 = num2 * quotient;
        const newAnswer = quotient;
        
        setQuestion({ num1, num2, answer: newAnswer });
        setUserAnswer('');
        setShowResult(false);
        setShowCelebration(false);
        setFeedback('');
        setQuestionStartTime(Date.now());
        
        // 
        setIsPlayingAudio(false);
        
        // 
        setTimeout(() => {
          playQuestionAudio(num1, num2);
        }, 1200);
      } else {
        await new Promise(resolve => setTimeout(resolve, 500));
        await playNumber(correctAnswer);
        setIsPlayingAudio(false);
      }
    } catch (error) {
      console.error(':', error);
      setIsPlayingAudio(false);
    }
  }, [playNumber, playQuestionAudio]);

  // 
  useEffect(() => {
    // 
    const timer = setTimeout(() => {
      generateQuestion();
    }, 300);
    
    return () => clearTimeout(timer);
  }, []); // 

  // 
  useEffect(() => {
    return () => {
      StudyDataStorage.endSession();
    };
  }, [sessionId]);

  // 
  useEffect(() => {
    const handleBeforeUnload = () => {
      StudyDataStorage.endSession();
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, [sessionId]);

  // 
  const checkAnswer = useCallback(() => {
    const answer = parseInt(userAnswer);
    const correct = answer === question.answer;
    const timeSpent = Date.now() - questionStartTime;
    
    // 
    StudyDataStorage.recordAnswer(
      `${question.num1}  ${question.num2}`,
      answer.toString(),
      question.answer.toString(),
      'division',
      'easy',
      timeSpent
    );
    
    setIsCorrect(correct);
    setShowResult(true);
    
    setStats(prev => ({
      correct: prev.correct + (correct ? 1 : 0),
      total: prev.total + 1,
      stars: prev.stars + (correct ? 1 : 0)
    }));
    
    if (correct) {
      setFeedback('');
      setShowCelebration(true);
      // 
      playAnswerFeedback(true, answer, question.answer);
    } else {
      setFeedback(` ${question.answer}`);
      // 
      playAnswerFeedback(false, answer, question.answer);
    }
  }, [userAnswer, question.answer, questionStartTime, playAnswerFeedback]);

  // 
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    if (/^\d*$/.test(value)) { // 
      setUserAnswer(value);
    }
  };

  // 
  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && userAnswer && !showResult) {
      checkAnswer();
    }
  };

  // 
  const CelebrationAnimation = () => (
    <div className="fixed inset-0 pointer-events-none z-50">
      {[...Array(20)].map((_, i) => (
        <div
          key={i}
          className="absolute animate-bounce"
          style={{
            left: `${Math.random() * 100}%`,
            top: `${Math.random() * 100}%`,
            animationDelay: `${Math.random() * 2}s`,
            animationDuration: `${1 + Math.random()}s`
          }}
        >
          <Star className="w-6 h-6 text-yellow-400 fill-current" />
        </div>
      ))}
    </div>
  );

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-400 via-pink-500 to-red-500 p-4">
      {showCelebration && <CelebrationAnimation />}
      
      <div className="max-w-2xl mx-auto">
        {/*  */}
        <div className="flex items-center justify-between mb-8">
          <button
            onClick={onBack}
            className="flex items-center gap-2 px-4 py-2 bg-white/20 backdrop-blur-sm rounded-lg text-white hover:bg-white/30 transition-colors"
          >
            <ArrowLeft className="w-5 h-5" />
            
          </button>
          
          <div className="text-center">
            <h1 className="text-2xl font-bold text-white mb-2"></h1>
            <div className="flex items-center gap-4 text-white/90">
              <div className="flex items-center gap-1">
                <Star className="w-5 h-5 text-yellow-300 fill-current" />
                <span>{stats.stars}</span>
              </div>
              <div className="flex items-center gap-1">
                <Heart className="w-5 h-5 text-red-300 fill-current" />
                <span>{stats.correct}/{stats.total}</span>
              </div>
            </div>
          </div>
          
          <button
            onClick={generateQuestion}
            className="flex items-center gap-2 px-4 py-2 bg-white/20 backdrop-blur-sm rounded-lg text-white hover:bg-white/30 transition-colors"
          >
            <RefreshCw className="w-5 h-5" />
            
          </button>
        </div>

        {/*  */}
        <div className="bg-white/95 backdrop-blur-sm rounded-2xl p-8 shadow-2xl mb-6">
          <div className="text-center mb-8">
            <div className="text-6xl font-bold text-gray-800 mb-4">
              {question.num1}  {question.num2} = ?
            </div>
            
            <button
              onClick={replayQuestion}
              className="flex items-center gap-2 mx-auto px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
              disabled={isPlayingAudio}
            >
              <Volume2 className="w-5 h-5" />
              {isPlayingAudio ? '...' : ''}
            </button>
          </div>

          <div className="flex flex-col items-center gap-4">
            <input
              type="text"
              value={userAnswer}
              onChange={handleInputChange}
              onKeyPress={handleKeyPress}
              placeholder=""
              className="text-4xl text-center border-2 border-gray-300 rounded-lg px-4 py-3 w-48 focus:border-blue-500 focus:outline-none"
              disabled={showResult}
            />
            
            {!showResult && (
              <button
                onClick={checkAnswer}
                disabled={!userAnswer}
                className="px-8 py-3 bg-green-500 text-white text-xl font-semibold rounded-lg hover:bg-green-600 disabled:bg-gray-300 disabled:cursor-not-allowed transition-colors"
              >
                
              </button>
            )}
          </div>

          {/*  */}
          {showResult && (
            <div className="mt-6 text-center">
              <div className={`text-2xl font-bold mb-4 ${
                isCorrect ? 'text-green-600' : 'text-red-600'
              }`}>
                {feedback}
              </div>
              
              {!isCorrect && (
                <button
                  onClick={generateQuestion}
                  className="px-6 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
                >
                  
                </button>
              )}
            </div>
          )}
        </div>

        {/*  */}
        <div className="bg-white/20 backdrop-blur-sm rounded-xl p-6 text-white">
          <h3 className="text-lg font-semibold mb-4 text-center"></h3>
          <div className="grid grid-cols-3 gap-4 text-center">
            <div>
              <div className="text-2xl font-bold">{stats.total}</div>
              <div className="text-sm opacity-90"></div>
            </div>
            <div>
              <div className="text-2xl font-bold">{stats.correct}</div>
              <div className="text-sm opacity-90"></div>
            </div>
            <div>
              <div className="text-2xl font-bold">
                {stats.total > 0 ? Math.round((stats.correct / stats.total) * 100) : 0}%
              </div>
              <div className="text-sm opacity-90"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DivisionPractice;