import React, { useState, useEffect, useCallback } from 'react';
import { ArrowLeft, RefreshCw, Star, Heart, MoreHorizontal } from 'lucide-react';
import { useAudio } from '../../hooks/useAudio';
import StudyDataStorage from '../../lib/studyDataStorage';

interface ComparisonGameProps {
  onBack: () => void;
}

interface Question {
  num1: number;
  num2: number;
  correctAnswer: '>' | '<' | '=';
}

interface Stats {
  correct: number;
  total: number;
  stars: number;
}

type ComparisonOperator = '>' | '<' | '=';

const ComparisonGame: React.FC<ComparisonGameProps> = ({ onBack }) => {
  const { playNumber, playCorrect, playWrong } = useAudio();
  const [question, setQuestion] = useState<Question>({ num1: 0, num2: 0, correctAnswer: '=' });
  const [selectedAnswer, setSelectedAnswer] = useState<ComparisonOperator | null>(null);
  const [stats, setStats] = useState<Stats>({ correct: 0, total: 0, stars: 0 });
  const [showResult, setShowResult] = useState(false);
  const [isCorrect, setIsCorrect] = useState(false);
  const [showCelebration, setShowCelebration] = useState(false);
  const [feedback, setFeedback] = useState('');
  const [draggedOperator, setDraggedOperator] = useState<ComparisonOperator | null>(null);
  const [sessionId] = useState(() => StudyDataStorage.startSession('game'));
  const [questionStartTime, setQuestionStartTime] = useState<number>(Date.now());
  const [isPlayingAudio, setIsPlayingAudio] = useState(false);
  const [audioError, setAudioError] = useState<string | null>(null);

  // 
  const [audioAbortController, setAudioAbortController] = useState<AbortController | null>(null);

  // 
  const playQuestionAudio = useCallback(async (num1: number, num2: number) => {
    // 
    if (audioAbortController) {
      audioAbortController.abort();
    }
    
    if (isPlayingAudio) {
      setIsPlayingAudio(false);
      // 
      await new Promise(resolve => setTimeout(resolve, 100));
    }
    
    const controller = new AbortController();
    setAudioAbortController(controller);
    setIsPlayingAudio(true);
    setAudioError(null);
    
    try {
      // 
      if (controller.signal.aborted) return;
      
      // 500ms
      await new Promise((resolve, reject) => {
        const timeout = setTimeout(resolve, 500);
        controller.signal.addEventListener('abort', () => {
          clearTimeout(timeout);
          reject(new Error('Audio playback cancelled'));
        });
      });
      
      if (controller.signal.aborted) return;
      await playNumber(num1);
      
      // 1000ms
      await new Promise((resolve, reject) => {
        const timeout = setTimeout(resolve, 1000);
        controller.signal.addEventListener('abort', () => {
          clearTimeout(timeout);
          reject(new Error('Audio playback cancelled'));
        });
      });
      
      if (controller.signal.aborted) return;
      await playNumber(num2);
      
    } catch (error) {
      if (error instanceof Error && error.message === 'Audio playback cancelled') {
        console.log('');
        return;
      }
      console.error(':', error);
      setAudioError('');
    } finally {
      if (!controller.signal.aborted) {
        setIsPlayingAudio(false);
        setAudioAbortController(null);
      }
    }
  }, [playNumber, isPlayingAudio, audioAbortController]);

  // 
  const generateQuestion = useCallback(() => {
    let num1, num2, correctAnswer: ComparisonOperator;
    
    //  (1-50)
    num1 = Math.floor(Math.random() * 50) + 1;
    num2 = Math.floor(Math.random() * 50) + 1;
    
    // 
    if (num1 > num2) {
      correctAnswer = '>';
    } else if (num1 < num2) {
      correctAnswer = '<';
    } else {
      correctAnswer = '=';
    }
    
    setQuestion({ num1, num2, correctAnswer });
    setSelectedAnswer(null);
    setShowResult(false);
    setShowCelebration(false);
    setFeedback('');
    setAudioError(null);
    setQuestionStartTime(Date.now());
    
    // 
    setTimeout(() => {
      playQuestionAudio(num1, num2);
    }, 600);
  }, [playQuestionAudio]);

  // 
  useEffect(() => {
    // 
    const timer = setTimeout(() => {
      generateQuestion();
    }, 300);
    
    return () => clearTimeout(timer);
  }, []); // 

  // 
  useEffect(() => {
    return () => {
      // 
      if (audioAbortController) {
        audioAbortController.abort();
      }
      StudyDataStorage.endSession();
    };
  }, [sessionId, audioAbortController]);

  // 
  useEffect(() => {
    const handleBeforeUnload = () => {
      StudyDataStorage.endSession();
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, [sessionId]);

  // 
  const playAnswerFeedback = useCallback(async (correct: boolean) => {
    try {
      setIsPlayingAudio(false); // 
      
      if (correct) {
        // 
        await playCorrect();
        // 
        setTimeout(() => {
          setIsPlayingAudio(false);
          generateQuestion();
        }, 1200);
      } else {
        // 
        await playWrong();
        setIsPlayingAudio(false);
      }
    } catch (error) {
      console.error(':', error);
      setIsPlayingAudio(false);
    }
  }, [playCorrect, playWrong, generateQuestion]);

  // 
  const checkAnswer = useCallback((answer: ComparisonOperator) => {
    const correct = answer === question.correctAnswer;
    const timeSpent = Date.now() - questionStartTime;
    
    // 
    StudyDataStorage.recordAnswer(
       `${question.num1} ? ${question.num2}`,
       answer,
       question.correctAnswer,
       'mixed',
       'easy',
       timeSpent
     );
    
    setSelectedAnswer(answer);
    setIsCorrect(correct);
    setShowResult(true);
    
    setStats(prev => ({
      correct: prev.correct + (correct ? 1 : 0),
      total: prev.total + 1,
      stars: prev.stars + (correct ? 1 : 0)
    }));
    
    if (correct) {
      setFeedback('');
      setShowCelebration(true);
      playAnswerFeedback(true);
    } else {
      setFeedback(` ${question.num1} ${question.correctAnswer} ${question.num2}`);
      playAnswerFeedback(false);
    }
  }, [question, playAnswerFeedback]);

  // 
  const handleDragStart = (e: React.DragEvent, operator: ComparisonOperator) => {
    setDraggedOperator(operator);
    e.dataTransfer.effectAllowed = 'move';
  };

  // 
  const handleDragEnd = () => {
    setDraggedOperator(null);
  };

  // 
  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    if (draggedOperator && !showResult) {
      checkAnswer(draggedOperator);
    }
  };

  // 
  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
  };

  // 
  const CelebrationAnimation = () => (
    <div className="fixed inset-0 pointer-events-none z-50">
      {[...Array(20)].map((_, i) => (
        <div
          key={i}
          className="absolute animate-bounce"
          style={{
            left: `${Math.random() * 100}%`,
            top: `${Math.random() * 100}%`,
            animationDelay: `${Math.random() * 2}s`,
            animationDuration: `${1 + Math.random()}s`
          }}
        >
          {['', '', '', '', ''][Math.floor(Math.random() * 5)]}
        </div>
      ))}
    </div>
  );

  return (
    <div className="max-w-3xl mx-auto">
      {/*  */}
      <div className="bg-white rounded-2xl shadow-lg p-4 mb-6">
        <div className="flex justify-between items-center">
          <div className="flex items-center gap-4">
            <div className="flex items-center gap-1">
              <Star className="w-5 h-5 text-yellow-500" />
              <span className="font-bold text-yellow-600">{stats.stars}</span>
            </div>
            <div className="text-sm text-gray-600">
              : {stats.total > 0 ? Math.round((stats.correct / stats.total) * 100) : 0}%
            </div>
            <div className="text-sm text-gray-600">
              : {stats.total} 
            </div>
          </div>
          <button
            onClick={generateQuestion}
            className="flex items-center gap-2 px-3 py-1 bg-blue-100 text-blue-600 rounded-full hover:bg-blue-200 transition-colors"
          >
            <RefreshCw className="w-4 h-4" />
            <span className="text-sm font-medium"></span>
          </button>
        </div>
      </div>

      {/*  */}
      <div className="text-center mb-6">
        <p className="text-gray-600"></p>
        
        {/*  */}
        {isPlayingAudio && (
          <div className="mt-3 flex items-center justify-center gap-2 text-blue-600">
            <div className="animate-pulse w-2 h-2 bg-blue-600 rounded-full"></div>
            <span className="text-sm font-medium">...</span>
          </div>
        )}
        
        {/*  */}
        {audioError && (
          <div className="mt-3 flex items-center justify-center gap-2 text-red-600 bg-red-50 rounded-lg px-4 py-2">
            <span className="text-sm font-medium">{audioError}</span>
            <button
              onClick={() => playQuestionAudio(question.num1, question.num2)}
              className="text-xs bg-red-100 hover:bg-red-200 px-2 py-1 rounded transition-colors"
            >
              
            </button>
          </div>
        )}
      </div>

      {/*  */}
      <div className="bg-white rounded-3xl shadow-2xl p-8 mb-6">
        <div className="text-center">
          {/*  */}
          <div className="flex items-center justify-center gap-8 mb-8">
            {/*  */}
            <div className="bg-gradient-to-br from-blue-400 to-blue-600 text-white text-4xl font-bold w-28 h-28 rounded-3xl flex items-center justify-center shadow-2xl transform hover:scale-105 transition-transform">
              {question.num1}
            </div>

            {/*  */}
            <div 
              className={`w-24 h-24 border-4 border-dashed border-gray-300 rounded-2xl flex items-center justify-center text-4xl font-bold transition-all duration-300 ${
                showResult 
                  ? (isCorrect ? 'bg-green-100 border-green-400' : 'bg-red-100 border-red-400')
                  : 'hover:border-blue-400 hover:bg-blue-50'
              }`}
              onDrop={handleDrop}
              onDragOver={handleDragOver}
            >
              {selectedAnswer || (
                <span className="text-gray-400 text-2xl">?</span>
              )}
            </div>

            {/*  */}
            <div className="bg-gradient-to-br from-green-400 to-green-600 text-white text-4xl font-bold w-28 h-28 rounded-3xl flex items-center justify-center shadow-2xl transform hover:scale-105 transition-transform">
              {question.num2}
            </div>
          </div>

          {/*  */}
          {!showResult && (
            <div className="flex justify-center gap-6 mb-6">
              {(['>', '<', '='] as ComparisonOperator[]).map((operator) => (
                <div key={operator} className="flex flex-col items-center gap-2">
                  {/*  */}
                  <button
                    draggable
                    onDragStart={(e) => handleDragStart(e, operator)}
                    onDragEnd={handleDragEnd}
                    onClick={() => checkAnswer(operator)}
                    className={`w-20 h-20 text-4xl font-bold rounded-2xl shadow-lg transition-all duration-300 transform hover:scale-110 active:scale-95 ${
                      draggedOperator === operator
                        ? 'bg-yellow-400 text-white scale-110'
                        : 'bg-white text-gray-700 hover:bg-gray-50'
                    }`}
                  >
                    {operator}
                  </button>
                  {/*  */}
                  <span className="text-sm text-gray-500 font-medium">
                    {operator === '>' ? '' : operator === '<' ? '' : ''}
                  </span>
                </div>
              ))}
            </div>
          )}

          {/*  */}
          {showResult && (
            <div className={`p-6 rounded-2xl ${isCorrect ? 'bg-green-100' : 'bg-red-100'}`}>
              <div className={`text-2xl font-bold mb-4 ${isCorrect ? 'text-green-600' : 'text-red-600'}`}>
                {feedback}
              </div>
              {!isCorrect && (
                <button
                  onClick={generateQuestion}
                  className="bg-blue-500 hover:bg-blue-600 text-white font-bold px-6 py-3 rounded-xl transition-colors"
                >
                   
                </button>
              )}
            </div>
          )}
        </div>
      </div>

      {/*  */}
      <div className="bg-blue-50 rounded-2xl p-4 mb-6">
        <div className="flex items-center justify-center gap-4 text-blue-600">
          <div className="flex items-center gap-2">
            <span className="text-2xl"></span>
            <span className="font-medium"></span>
          </div>
          <span className="text-blue-400"></span>
          <div className="flex items-center gap-2">
            <span className="text-2xl"></span>
            <span className="font-medium"></span>
          </div>
        </div>
      </div>

      {/*  */}
      <div className="text-center">
        <div className="inline-flex items-center gap-2 bg-white rounded-full px-6 py-3 shadow-lg">
          <Heart className="w-5 h-5 text-red-500" />
          <span className="text-gray-600 font-medium">
            {stats.correct === 0 ? '' :
             stats.correct < 5 ? '' :
             stats.correct < 10 ? '' :
             ''}
          </span>
        </div>
      </div>

      {/*  */}
      {showCelebration && <CelebrationAnimation />}
    </div>
  );
};

export default ComparisonGame;